@import '@dpe/base/src/global';

// Tabbed
//
// Package: `@dpe/tabbed`
//
// A custom vertical tabbed component shown with a toggle on small screens
// and sidebar menu on larger ones.
//
// ### DPE javascript:
//
// Requires: `/js/tabbed.js`\
// Dependency: `/js/polyfill.js`\
// Usage:
// ```
// document.querySelectorAll('.tabbed').forEach((obj) => {
//    const tabbedComponent = new Tabbed(obj);
//    tabbedComponent.init();
// });
// ```
//
// Markup: tabbed.twig
//
// Style guide: 7.tabbed

.tabbed {
  display: flex;
  flex-direction: column;
  margin: 0 0 $paragraph-margin-bottom;
  border: $border-style;
  border-radius: $border-radius;

  @include media-breakpoint-up(lg) {
    display: block;
    margin: 0;
  }

  &-container {
    display: flex;
    flex-direction: column;
    position: relative;
    overflow: hidden;
    order: 2;

    @include media-breakpoint-up(lg) {
      flex-direction: row;
    }
  }

  &-title {
    font-size: $text-xs;
    border-bottom: $border-style;
    padding: $spacer-sm ($spacer-m + $spacer-xs);
    margin-bottom: $spacer-sm;
  }

  &-menu {
    display: none;
    flex: 1;
    z-index: 1;
    width: 100%;
    background: $background;
    position: absolute;
    top: 0;
    overflow: auto;
    max-height: 100%;

    &.active {
      display: block;
    }

    @include media-breakpoint-up(lg) {
      position: relative;
      display: block;
      top: 0;
      max-height: none;
      box-shadow: none;
    }

    ul {
      padding: $spacer-xs;
      margin-bottom: 0;
    }

    a {
      font-size: $text-xs;
      color: $foreground;
      display: block;
      padding: $spacer-sm $spacer-m $spacer-sm $spacer-ml;
      text-decoration: none;
      border-radius: $border-radius;
      margin-top: 1px;

      &:hover {
        text-decoration: none;
        background-color: $background-alt;
      }

      &.active {
        @include icon('chevron-right-white');
        background-position: right $spacer-s center;
        background-color: $background-rev;
        color: $foreground-rev;
      }
    }
  }

  &-content {
    position: relative;
    flex: 3;

    ul {
      display: block;
      height: 100%;
      margin-bottom: 0;
    }

    li {
      height: 100%;
    }
  }

  &-item {
    background: $background-alt;
    padding: $spacer-xs;
    position: relative;
    display: none;
    width: 100%;
    max-width: none;

    &.active {
      display: block;
    }
  }

  &-teaser {
    position: relative;
    width: 100%;
    padding: $spacer-ml;

    h3 {
      color: $primary;
    }

    p {
      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &-toggle {
    font-size: $text-xs;
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 50px;
    text-align: left;
    padding-left: $spacer-m;
    padding-right: $spacer-m;
    order: 1;
    background-color: $background;
    margin: $spacer-xs;
    border: $border-style;
    width: calc(100% - #{$spacer-xs * 2});
    border-radius: $border-radius;
    color: $link-color;
    font-weight: $font-weight-bold;

    &::after {
      @include icon('chevron-down');
      content: '';
      display: block;
      position: relative;
      height: $spacer-m;
      width: $spacer-m;
    }

    @include media-breakpoint-up(lg) {
      display: none;
    }
  }
}
